
.container {
    margin: 0 0 0 0; 
}

.top-item {
    padding-top: 10em;
    padding-bottom: 10em;
}

.second-item {
    padding-top: 10em;
    background: #343334;
    background: linear-gradient(180deg, rgba(52,51,52,1) 0%, rgba(88,88,88,1) 100%);
}

.second-item + .nxt-movies {
    background: rgb(88,88,88);
    background: linear-gradient(180deg, rgba(88,88,88,1) 0%, rgba(255,255,255,1) 80%);
}

.nxt-movies {
    padding-bottom: 3em;
}

.movie-block:last-child {
    padding-bottom: 12em;
}

#movie-title {
    font-size: 30px;
}

.nxt-movies {
    padding-bottom: 3em;
    color: #ffffff;
}

.movie-block {
    display: flex;
    width: 100vw;  
    align-items: center; 
}

.movie-block p{
    font-size: 20px;
}

.movie-img-block {
    display: flex;
}

#movie-img {
    width: 70%;
}